.navigation {
    width: 100%;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: start;
    position: absolute;
    top: 0px;
}

.navigation-elements {
    width: 300px;
    height: 40px;
    margin-left: 100px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    position: relative;
}

.elements {
    height: 40px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    display: flex;
    align-items: center;
    justify-content: center;
}

.elements a {
    text-decoration: none;
    color: #FFF;
}

.nav-target {
    width: 90px;
    height: 1px;
    background-color: #FFF;
    position: absolute;
    top: 50%;
    left: 5%;
    transition: 1s ease;
}

.elements:nth-child(1):hover~.nav-target {
    left: 5%;
}

.elements:nth-child(2):hover~.nav-target {
    left: 48%;
    width: 140px;
}

@media only screen and (max-width: 600px) {
    .navigation-elements {
        margin-left: 5%;
    }
}