@import url('https://fonts.googleapis.com/css2?family=Archivo+Black&family=Red+Hat+Display:ital,wght@0,300..900;1,300..900&display=swap');

@font-face {
  font-family: 'Akira';
  src: url('./fonts/AkiraExpandedDemo.otf') format('opentype'),
}

@font-face {
  font-family: 'LEMONMILK-Bold';
  src: url('./fonts/LEMONMILK-Bold.otf') format('opentype'),
}

@font-face {
  font-family: 'LEMONMILK-Regular';
  src: url('./fonts/LEMONMILK-Regular.otf') format('opentype'),
}

* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family:  "LEMONMILK-Regular", "Red Hat Display", serif;
  overflow: hidden;
}

body p {
  font-size: 12px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.font-akira {
  font-family: "Akira", "Red Hat Display";
}

.font-LEMONMILK-Bold {
  font-family: "LEMONMILK-Bold", "Red Hat Display";
}

.font-LEMONMILK-Regular {
  font-family: "LEMONMILK-Regular", "Red Hat Display";
}

.container {
  width: 100%;
  height: 100vh;
  transition: all 1s;
}

.home { 
  background-color: #101010;
  transition: all 1s;
  color: #FFF;
}

.projects { 
  background-color: #fff;
  transition: all 1s;
  color: #101010;
  overflow: auto;
}

.in-container {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.in-container .title {
  text-align: center;
}

.in-container .title h2 {
  font-size: 55px;
}

.table-container {
  height: 500px;
  width: 450px;
  margin-top: 40px;
}

.project {
  width: 90%;
  height: 100px;
  border-bottom: 1px solid #000;
  margin-top: 10px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0px 10px;
}

.projects-text p:nth-child(1) {
  font-family: "Akira", "Red Hat Display";
  font-size: 17px;
}

.projects-text p:nth-child(2) {
  max-width: 70%;
  font-size: 11px;
  margin-top: 10px;
}

.like-sys {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: row-reverse;
  width: 40px;
  height: 18px;
  cursor: pointer;
}

@media only screen and (max-width: 600px) {  
  .in-container .title h2 {
    font-size: 35px;
  }

  .table-container {
    height: 400px;
    width: 350px;
  }

  .like-sys p {
    font-size: 10px;
  }
}

.container-header {
  width: 60%;
  margin-top: 15%;
}

.article {
  justify-content: start;
  overflow: auto;
}

.header-links {
  margin-top: 15px;
  font-family: "Red Hat Display", serif;
  display: flex;
  align-items: start;
  flex-direction: column;
}

.header-links span {
  font-size: 15px;
  margin-top: 10px;
}

.header-links span a {
  color: #fff;
}

.description {
  font-size: 16px;
  margin-top: 20px;
  font-family: "Red Hat Display", serif;
}

.like-active { fill: #ff7c7c; }
.like-white { fill: #FFF; }
.like-dark { fill: #1a1a1a; }

.disable { display: none; }

@media only screen and (max-width: 600px) {
  .container-header {
    width: 80%;
    margin-top: 30%;
  }
  
}