* {
    margin: 0;
    padding: 0;
}

.not-found {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: #FFF;
    background-color: #101010;
}

.not-found-title {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-bottom: 10px;
}

.not-found- {
    font-size: 45px !important;
    letter-spacing: 2px;
}

.button {
    background: #303030;
    padding: 5px 20px;
    border-radius: 6px;
    text-decoration: none;
    color: #fff;
    font-size: 11px;
    letter-spacing: 1px;
}